<template>
    <div class="ranking">
        <div class="list-top" v-if="tableData">
            <rank-tab-two :tab-list="theadName" :list-type="listType"></rank-tab-two>
        </div>
        <div class="main-table" v-if="tableData && tableData.hasOwnProperty('list')" v-loading="loading">
            <el-table
                    :data="tableData.list"
                    :header-cell-style="{background:'#f0f1f5',color:'#666'}"
                    :empty-text="$L('暂无数据')"
                    style="width: 100%" class="table">
                <el-table-column
                        prop="noId"
                        :label="$L('排名')"
                        align="center">
                    <div slot-scope="scope">
                        <p class="noid"><span class="bbx-font bbx-icon-huiyuan1"></span> {{ scope.row.noId }}</p>
                    </div>
                </el-table-column>
                <el-table-column
                        prop="agentName"
                        :label="$L('用户名')"
                        align="center">
                    <div slot-scope="scope">
                        {{ scope.row.agentName ? scope.row.agentName : $L('-') }}
                    </div>
                </el-table-column>
                <el-table-column
                        prop="realName"
                        :label="$L('姓名')"
                        align="center">
                    <div slot-scope="scope">
                        {{ scope.row.realName ? scope.row.realName : $L('-') }}
                    </div>
                </el-table-column>
                <el-table-column
                        prop="sellTotal"
                        :label="$L('交易总额')"
                        align="center" v-if="listType.type === 'sales'">
                </el-table-column>
                <el-table-column
                        prop="sellTotal"
                        :label="$L('佣金')"
                        align="center" v-if="listType.type === 'commission'">
                </el-table-column>
                <el-table-column
                        prop="sellTotal"
                        :label="$L('下级个数')"
                        align="center" v-if="listType.type === 'offline'">
                </el-table-column>
            </el-table>
            <!-- 分页 -->
            <div class="list-pages" v-if="tableData.allNum > 0 && tableData.allNum > pageSize">
                <el-pagination
                        background
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-size="10"
                        layout="prev, pager, next, jumper"
                        :total="tableData.allNum">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    import rankTabTwo from "../../../components/common/rankTabTwo.vue"
    export default {
        name: "ranking",
        components : {
            rankTabTwo
        },
        data (){
            return {
                loading:true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                btnSize:'small',
                tableData: {},
                pageSize: 20,
                currentPage:1,
                inputValue:'',
                listType:{
                    type:'sales'
                }
            }
        },
        computed:{
            theadName:{
                get(){
                    let re = [];
                    if(this.tableData.sales === 1){
                        re.push({
                            text:this.$L('销售排行榜'),
                            type: 'sales',
                            func: () => {
                                this.$router.push({params:{type:'sales'}});
                            }
                        })
                    }
                    if(this.tableData.commission === 1){
                        re.push({
                            text:this.$L('佣金排行榜'),
                            type: 'commission',
                            func: () => {
                                this.$router.push({params:{type:'commission'}});
                            }
                        })
                    }
                    if(this.tableData.offline === 1){
                        re.push({
                            text:this.$L('下级排行榜'),
                            type: 'offline',
                            func: () => {
                                this.$router.push({params:{type:'offline'}});
                            }
                        })
                    }
                    return re;
                }
            },
        },
        beforeRouteUpdate (to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
            // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
            // 可以访问组件实例 `this`
            this.getData(to);
            this.currentPage = 1;
            next();
        },
        created() {
            this.getData();
        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData(router) {
                const _self = this;
                _self.loading = true;
                if(!router){
                    router = _self.$route;
                }
                _self.listType.type = router.params.hasOwnProperty('type') ? router.params.type : 'sales';
                let arr = {
                    p : _self.currentPage,
                    num : _self.pageSize,
                    type :  _self.listType.type,
                    keyword : _self.inputValue,
                };
                _self.$api('distributionRankingList', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;

                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回我的分销'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'distributorList'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.getData();
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            searchFunc(val){
                this.inputValue = val;
                this.getData();
            },
        },
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/icon/iconfont.css";
     .ranking{
         position: relative;
         padding: 0 20px 20px!important;
         .list-top{
             position: relative;
             padding-top: 10px;
             .right-font{
                 position: absolute;
                 right: 0;
                 display: inline-block;
                 .input-with-select{
                     width: 260px;
                     padding-left: 10px;
                     /deep/ .el-input-group__append{
                         font-size: 14px;
                         color: #333;
                         padding: 0 10px;
                         background: #fff;
                         border-left-color: #fff;
                     }
                 }
             }
         }
         .main-table{
             padding: 20px;
             background: #fff;
             margin-top: 10px;
             .table{
                 width: 100%;
                 .noid{
                     width: 40px;
                     margin: 0 auto;
                     .bbx-font{
                        visibility: hidden;
                     }
                 }
                 .el-table__row:nth-child(1){
                     .bbx-font{
                         visibility: visible;
                         color:#FCD23D;
                     }
                 }
                 .el-table__row:nth-child(2){
                     .bbx-font{
                         visibility: visible;
                         color:#DDDDDD;
                     }
                 }
                 .el-table__row:nth-child(3){
                     .bbx-font{
                         visibility: visible;
                         color:#8B4513;
                     }
                 }
             }
             .list-pages{
                 text-align: center;
                 padding: 40px 0 20px;
                 background: #fff;
             }
         }
         .error {
             position: absolute;
             z-index: 200;
             top: 0;
             left: 0;
             width: 100%;
             height: 100%;
             background: #FFFFFF;
             overflow: hidden;

             .error-tips {
                 position: absolute;
                 top: 50%;
                 left: 50%;
                 width: 320px;
                 transform: translate(-50%, -50%);
             }
         }
     }
</style>