<template>
    <div class="rank-tab-two" v-if="tabList && tabList.length> 0 && listType.hasOwnProperty('type')">
        <el-button-group class="btn-group">
            <el-button class="btn-item"
                       v-for="(item,index) in tabList"
                       :type="listType.type == item.type?'primary':'default'"
                       :key="index"
                       size="small"
                       @click="typeof item.func === 'function' ? item.func() : null">{{ item.text }}</el-button>
        </el-button-group>
    </div>
</template>

<script>
    export default {
        name:'rankTabTwo',
        props: {
            tabList: {
                type: Array,
                default: ()=>{
                    return [];
                }
            },
            listType: {
                type: Object,
                default: ()=>{
                    return {};
                }
            }
        },

    }
</script>

<style lang="scss" scope>
    .rank-tab-two{
        display: inline-block;
        .btn-group{
            .btn-item{
                width: 100px;
            }
        }
    }
</style>